import uniIcons from "../../components/uni-icons.vue";
import noticeItem from "../../components/notice-item.vue";

export default {
	components: {
		uniIcons,
		noticeItem,
	},
	data() {
		return {
			items: [],
			isLoading: false,
			loadingText: "已经到最底了~",
			isPulling: false,
			refreshFlag: false,
			isRefreshing: false,
			refreshText: "已刷新",
			scrollTop: {
				count: 0,
				old: 0,
				current: 0,
			},
			dataLenLimit: 8, // 显示加载提示的数据长度限制
		}
	},
	computed: {
		tabBarHeight() {
			let newHeight = 80;
			let scrollTop = this.scrollTop.old || 0;
			if (scrollTop > 48) {
				newHeight -= 48;
			} else {
				newHeight -= scrollTop;
			}
			return newHeight + "px";
		},
		tabBarFontSize() {
			let newSize = 36;
			let scrollTop = this.scrollTop.old || 0;
			if (scrollTop > 48) {
				newSize -= 16;
			} else {
				newSize -= Math.floor(scrollTop/3);
			}
			return newSize + "px";
		},
		isShowTabBarLine() {
			let scrollTop = this.scrollTop.old || 0;
			if (scrollTop > 48) {
				return true;
			}
			return false;
		},
		tabContentHeight() {
			let newHeight = 100;
			let scrollTop = this.scrollTop.old || 0;
			if (scrollTop > 48) {
				newHeight -= 48;
			} else {
				newHeight -= scrollTop;
			}
			return "calc(100% - "+ newHeight +"px)";
		},
		searchInputWidth() {
			let newWidth = 280;
			let scrollTop = this.scrollTop.old || 0;
			if (scrollTop > 96) {
				newWidth -= 192;
			} else {
				newWidth -= 2 * scrollTop;
			}
			return newWidth + "px";
		}
	},
	onLoad() {
		this.loadTabData(null);
	},
	methods: {
		// 点击搜索按钮
		onSearch(e) {
			
		},
		// 滚动tab内容
		onScroll(e) {
			this.scrollTop.old = e.target.scrollTop || e.detail.scrollTop;
		},
		onGoTop(e) {
			this.scrollTop.count ++;
			setTimeout(() => {
				if (this.scrollTop.count > 1) {
					this.scrollTop.current = this.scrollTop.old;
					this.$nextTick(function() {
						this.scrollTop.current = 0;
					});
				}
				this.scrollTop.count = 0;
			}, 300);
		},
		// 加载tab数据
		loadTabData(callback) {
			// 标记正在加载
			this.isLoading = true;
			this.loadingText = "正在加载...";
			// 请求数据
			setTimeout(() => {
				for (let i = 0; i < 10; i++) {
					this.items.push({
						pic: "/static/img/logo.png",
						count: i * 9,
						name: "名称",
						time: "昨天",
						info: "对【工具名称什么的龙科恐龙丹佛的时刻v迫使对方的经费管理的方式对付苏联当局封锁的法律思考的客服就是每次v但是v了士大夫士大夫方式】进行了收藏",
					});
				}
				// 标记加载完成
				this.isLoading = false;
				this.loadingText = "加载更多...";
				if (callback != null) {
					callback();
				}
			}, 500);
		},
		// 加载更多
		loadMore(e) {
			this.loadTabData(null);
		},
		// 刷新tab数据
		refreshTabData(callback) {
			// 标记正在刷新
			this.isRefreshing = true;
			this.refreshText = "正在刷新...";
			// 请求数据
			setTimeout(() => {
				for (let i = 10; i > 0; i--) {
					this.items.unshift({
						pic: "/static/img/logo.png",
						count: 1,
						name: "名称",
						time: "昨天",
						info: "对【工具名称什么的龙科恐龙丹佛的时刻v迫使对方的经费管理的方式对付苏联当局封锁的法律思考的方式】进行了收藏",
					});
				}
				// 标记完成刷新
				this.isRefreshing = false;
				this.refreshText = "已刷新";
				if (callback != null) {
					callback();
				}
			}, 1000);
		},
		// 刷新tab
		onRefresh(e) {
			if (!this.refreshFlag) {
				return;
			}
			// 刷新tab数据
			this.refreshTabData(function() {
				this.isPulling = true;
				this.refreshFlag = false;
				setTimeout(() => { // Fix ios和Android 动画时间相反问题
					this.isPulling = false;
				}, 500);
			});
		},
		onPullingDown(e) {
			if (this.isRefreshing || this.isPulling) {
				return;
			}
			if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
				this.refreshFlag = true;
				this.refreshText = "释放立即刷新";
			} else {
				this.refreshFlag = false;
				this.refreshText = "下拉可以刷新";
			}
		},
		showDetail(e) {
			
		},
	}
}
